}


.Python-projects {
  display: inline-block;
  position: relative;
  color: #f0d502;
}

.Python-projects::after {
  content: '';
  
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 5px;
  bottom: 0;
  left: 0;
  background-color: #f0d502;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.Python-projects:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

